@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    /* Tailwind'in varsayılan stillerini geçersiz kılmak için */
    @apply bg-black; 
  }
}

.starry-background {
  @apply relative w-full h-screen overflow-hidden;
  
  /* Sarı yıldızlar için box-shadow efekti */
  box-shadow: 
    0px 0px 1px yellow,
    15px 15px 1px yellow,
    25px 25px 1px yellow,
    -10px -10px 1px yellow,
    -20px -20px 1px yellow,
    -30px -30px 1px yellow;
}

.starry-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black; /* Yıldızların daha belirgin olması için arka plan rengi */
  z-index: -1;
}

.animate-fade-in {
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #c7d2fe #f5f3ff;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #f5f3ff;
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #c7d2fe;
  border-radius: 3px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #a5b4fc;
}
